/* Import fonts */
@import url('./fonts.css');

/* Import global css */
@import url('./global.css');

/* import variables */
@import url('./variables.css');

.App {
    text-align: center;
  }
  
.Menu {
    text-align: center;
    margin-top: 20px;
}

.header {
    display: flex;
    justify-content: center;
    text-align: center;
    border-bottom: 5px solid var(--border-color);
    background:url('../images/sunrise4.jpeg')no-repeat 0% 100%;
    background-size: cover;
  }
  
  .header h1 {
    font-size: 80px;
    font-family: var(--KGAllofMe);
    letter-spacing: 0.07em;
    line-height: normal;
    text-align: center;
    padding-top: 70px;
    color: white;
  }
  
  .logo {
    margin: 1rem 12rem 1rem 0;

  }

  .landingpage {
    background-image: url('../images/sunrise3.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%; 
    width: 100%;
    position: fixed;
  }

 .landingpage h1 {
  font-size: 95px;
  font-family: var(--KGAllofMe);
  letter-spacing: 0.07em;
  line-height: normal;
  text-align: center;
  padding-top: 90px;
  color: white;
 }

 .menu-button {
   margin: 20px;
 }

 .landingpage-logo {
   width: 350px;
   margin: 50px;
 }

 .landing-btn {
   margin: 20px;
 }


