* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .upper-case {
      text-transform: uppercase;
    }
  
  .font-KGAll{
      font-family: var(--KGAllofMe);
  }
  
  /* Menu Area */
  .button {
    padding: .8rem 2.2rem;
    font: normal 500 16px/20px var(--KGAllofMe);
    position: relative;
    border: 3px solid white;
    border-radius: 4px;
  }
  
  .button.primary-button {
    background: var(--gradient-color);
    background-clip: padding-box;
    color: whitesmoke;
    transition: all 0.6s ease;
    box-shadow: var(--box-shadow);
  }
  
  .button.primary-button:hover{
    background: whitesmoke;
    background-clip: padding-box;
    color:var(--secondary-color);
  }
  
  .button.primary-button::after{
    position: absolute;
    top: -2px;
    left:-2px;
    bottom:-2px;
    right: -2px;
    background: var(--gradient-color);
    content: '';
    z-index: -1;
    border-radius: 4px;
  }
  
  .tab-title {
    font-family: "KGAllofMe", monospace;
    margin: 2rem;
  }
  
  table {
    margin-top: 5rem;
  }
  
  table th {
    font-family: "KGAllofMe", monospace;
    font-size: 22px;
    line-height: normal;
    letter-spacing: 0.2rem;
  }
  
  table td {
    font-family: "Raleway", monospace;
    font-size: 18px;
  }